// Colors
$color-primary: #4684e1b4;
$color-secondary: #0fa7f3b6;
$color-tertiary: #2edab7;
$color-quaternary: #88369e9c;

$color-grey-light: #efefef;
$color-grey-dark: #333;

$color-hover: #edf5f8;
$color-link-hover: #ccc;

$color-paragraph: #4a4a4a;

$color-green: #34eb49;

$color-white: #fff;
$color-black: rgb(10, 136, 204);


// Font
$default-font-size: 1.6rem;

// Grid
$grid-width: 114rem;
$gutter-vertical: 4rem;
$gutter-vertical-small: 2rem;
$gutter-horizontal: 4rem;
